'use strict';

export default class Matrix {

  private rows: number;
  private cols: number;
  private matrix: number[][];

  constructor(rows: number = 0, cols: number = 0) {
    this.rows = rows;
    this.cols = cols;
    this.matrix = [];
    for (let i = 0; i < rows; i++) {
      this.matrix[i] = new Array(cols).fill(0, 0, cols);
    }
  }

  public dot(m: Matrix): Matrix {
    if (this.cols !== m.rows) {
      console.log(m, this);
      throw new Error('Invalid sizes' + this.cols + ' ' + this.rows + ' ' + m.cols + ' ' + m.rows);
    }
    const res = new Matrix(this.rows, m.cols);
    for (let i = 0; i < this.rows; i++) {
      for (let j = 0; j < m.cols; j++) {
        for (let k = 0; k < this.cols; k++) {
          res.matrix[i][j] += this.matrix[i][k] * m.matrix[k][j];
        }
      }
    }
    return res;
  }

  public transpose(): Matrix {
    const res = new Matrix(this.cols, this.rows);
    for (let i = 0; i < this.rows; i++) {
      for (let j = 0; j < this.cols; j++) {
        res.matrix[j][i] = this.matrix[i][j];
      }
    }
    return res;
  }

  public randomize(): Matrix {
    for (let i = 0; i < this.rows; i++) {
      for (let j = 0; j < this.cols; j++) {
        this.matrix[i][j] = Math.random() - Math.random();
      }
    }
    return this;
  }

  public addNumber(n: number): Matrix {
    for (let i = 0; i < this.rows; i++) {
      for (let j = 0; j < this.cols; j++) {
        this.matrix[j][i] += n;
      }
    }
    return this;
  }

  public multiplyByNumber(n: number): Matrix {
    for (let i = 0; i < this.rows; i++) {
      for (let j = 0; j < this.cols; j++) {
        this.matrix[j][i] *= n;
      }
    }
    return this;
  }

  public rowFromArray(arr: number[]): Matrix {
    this.rows = 1;
    this.cols = arr.length;
    this.matrix = [arr];
    return this;
  }

  public colFromArray(arr: number[]): Matrix {
    this.cols = 1;
    this.rows = arr.length;
    this.matrix = arr.map(el => [el]);
    return this;
  }

  public toArray(): number[] {
    const res: number[] = [];
    this.matrix.forEach(row => row.forEach(el => res.push(el)));
    return res;
  }

  public activateSig(): Matrix {
    for (let i = 0; i < this.rows; i++) {
      for (let j = 0; j < this.cols; j++) {
        this.matrix[i][j] = sigmoid(this.matrix[i][j]);
      }
    }
    return this;
  }

  public mutate(mutationRate: number): Matrix {
    for (let i = 0; i < this.rows; i++) {
      for (let j = 0; j < this.cols; j++) {
        const rand = Math.random();
        if (rand < mutationRate) {
          this.matrix[i][j] += (Math.random() - 1) / 2.5;
          if (this.matrix[i][j] > 1) {
            this.matrix[i][j] = 1;
          }
          if (this.matrix[i][j] < -1) {
            this.matrix[i][j] = -1;
          }
        }
      }
    }
    return this;
  }

  public crossover(partner: Matrix): Matrix {
    const child = new Matrix(this.rows, this.cols);
    const randR = Math.floor(Math.random() * this.rows);
    const randC = Math.floor(Math.random() * this.cols);
    for (let i = 0; i < this.rows; i++) {
      for (let j = 0; j < this.cols; j++) {
        if (i < randR || (i === randR && j < randC)) {
          child.matrix[i][j] = this.matrix[i][j];
        } else {
          child.matrix[i][j] = partner.matrix[i][j];
        }
      }
    }
    return child;
  }

  public addBias(): Matrix {
    const n = new Matrix(1, this.cols + 1);
    for (let i = 0; i < this.cols; i++) {
      n.matrix[0][i] = this.matrix[0][i];
    }
    n.matrix[0][this.cols] = 1;
    return n;
  }

  public clone(): Matrix {
    const clone = new Matrix(this.rows, this.cols);
    for (let i = 0; i < this.rows; i++) {
      for (let j = 0; j < this.cols; j++) {
        clone.matrix[i][j] = this.matrix[i][j];
      }
    }
    return clone;
  }
}

function sigmoid(a: number): number {
  return 1 / (1 + Math.exp(a));
}
