'use strict';

import { NeuralNet } from './Net';

export class Bot {
  brain: NeuralNet;
  alive: boolean;
  fitness: number;

  constructor() {
    this.brain = new NeuralNet([16, 12, 12, 4]);
  }

  mutate(mr: number): void {
    this.brain.mutate(mr);
  }

  output(input: number[][]): number[] {
    const inp = [];
    let max = 0;
    for (let i = 0; i < 4; i++) {
      for (let j = 0; j < 4; j++) {
        inp.push(input[i][j]);
      }
    }
    max = Math.max(...inp);
    return this.brain.forwardPropagation(inp.map((el) => Math.max(Math.log(el) / Math.log(max), 0)));
  }

  crossover(partner: Bot) {
    const child = new Bot();
    child.brain = this.brain.crossover(partner.brain);
    return child;
  }

  clone(): Bot {
    const clone = new Bot();
    clone.brain = this.brain.clone();
    return clone;
  }
}
